// example
export const changeWeight = (weight) => ({
    type: 'CHANGE_WEIGHT',
    weight
});

export const changeHeight = (height) =>({
    type: 'CHANGE_HEIGHT',
    height
});

export const changeGender = (gender) =>({
    type: 'CHANGE_GENDER',
    gender
});

export const changeAge = (age) =>({
    type: 'CHANGE_AGE',
    age
});